.main-credential {
    font-family: 'Montserrat', sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: -20px 0 0px;
		margin-top: 20px;
}

h1 {
    font-weight: bold;
    margin: 0;
}
h3{
    font-weight: bold;
    color: #0040ff;
}
p {
    font-size: 14px;
    font-weight: 100;
    line-height: 20px;
    letter-spacing: .5px;
    margin: 20px 0 30px;
}

span {
    font-size: 13px;
}

a {
    color: #333;
    font-size: 14px;
    text-decoration: none;
    margin: 1px 0;
}
a:hover{
    text-decoration: none;
    color: #0040ff;
}
::-webkit-input-placeholder {
    color: #3a3a3a;
    font-weight: 600;
}
::placeholder {
    color: #3a3a3a;
    font-weight: 600;
}
nav{
    background: #191919;
}
.heading{
    margin: 0 auto;
    letter-spacing: -1.6px;
    font-family: 'Poppins', sans-serif;
    font-weight: 900px;
    font-size: 22.5px;
    color: #0040ff;
}
.nav-head-span-1{
    font-size: 22.5px;
    background-image: linear-gradient(to right, #0040ff,white);
    -webkit-background-clip:text;
            background-clip:text;
    color:transparent;
}
.nav-head-span-2{
    color: #FFF;
    font-size: 22.5px;
}
.fixed-top{
    border-bottom: 1.5px solid rgb(35, 35, 35);
}
.navbar .navbar-brand{
    color: #0040ff;
    
}
.form-title{
    margin-top: 20px;
}
.form-container form {
    margin: 0 15px;
    background: #191919;
    display: flex;
    flex-direction: column;
    padding:  0 50px;
    height: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.social-container {
    margin: 20px 0;
}

.SignIn-Form{
    border-radius: 7px;
}

.form-container input {
    background: #eee;
    border: none;
    outline: none;
    border-radius: 8px;
    padding: 12px 15px;
    margin: 8px 0;
    width: 100%;
}
.signin-text{
    color: #fff;
}
.google-signin-btn.signin-text:hover{
    color: #000000;
}

.sign-up-container{
    margin-top: 55rem;
}

.google-signin-btn{
    border: 1px solid #0040ff;
    color: #fff;
    font-size: 16.5px;
    font-weight: bolder;
}
.google-signin-btn:hover{
    background: #0040ff;
    color: #000000;
    border: 1px solid #0040ff;
    box-shadow: 0 0 10px #0040ff;
}

button {
    font-family: sans-serif;
    border-radius: 8px;
    border: 1px solid #0040ff;
    background: rgb(17, 17, 17);
    color: #fff;
    padding: 12px 45px;
    transition: .10s ease-in;
}

button:active {
    -webkit-transform: scale(.95);
            transform: scale(.95);
}
button:hover{
    background: #0040ff;
    color: rgb(0, 0, 0);
    box-shadow: 0px 0px 15px #0040ff;
}

button:focus {
    outline: none;
}

button.ghost {
    color: #000000;
    background: transparent;
    border-color: rgb(0, 0, 0);
}
button.ghost:hover {
    background: rgb(22, 22, 22);
    color: #000;
    border: none;
    box-shadow: 0px 0px 15px #000000;
}






.footer {
	margin-top: 25px;
	text-align: center;
}


@media (max-width: 765px) {
    .navbar-brand {
        font-size: 0px;
    }
}
